.App {
  display: flex;
  flex-direction: column;
  margin-bottom: 2vh;
}

h1 {
  font-size: 16vh;
  text-align: center;
}

h3 {
  font-size: 5vh;
  text-align: center;
}

.Form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input[type="text"] {
  width: 30vh;
  height: 10vh;
  font-size: 4vh;
  margin-bottom: 2vh;
}

button[type="submit"] {
  width: 30vh;
  height: 10vh;
  font-size: 4vh;
  margin: 1vh;
}